                          'mraniye': [41.0256362, 29.0963049],
                          'Beikta': [41.0428465, 29.0075283],
                          'Esenyurt': [41.0342862, 28.6801113],
                          'Saryer': [41.1685803, 29.0572623],
                          'Pendik': [40.8768715, 29.2349672],
                          'Fatih': [41.0091982, 28.9662187],
                          'Bykekmece': [41.021654, 28.579757],
                          'Eypsultan': [41.0478358, 28.9327383],
                          'Baakehir': [41.1075794, 28.7950711],
                          'Beyolu': [41.0284233, 28.9736808],
                          'Kartal': [40.88858, 29.1856536],
                          'Sultangazi': [41.1043344, 28.8614367],
                          'Sultanbeyli': [40.9670242, 29.2671314],
                          'Kadky': [40.990992, 29.0254305],
                          'ekmeky': [41.0351579, 29.1739149],
                          'Maltepe': [40.9247539, 29.1310782],
                          'Tuzla': [40.8161732, 29.3034194],
                          'Kathane': [41.0812087, 28.9730151],
                          'Zeytinburnu': [40.9898653, 28.9037467],
                          'skdar': [41.0265498, 29.0151321],
                          'Kkekmece': [40.9918737, 28.7711956],
                          'Bakrky': [40.9782585, 28.8744461],
